.newsWrapper {
            box-sizing: border-box;
            opacity:   0;
            transform: translateX(-100%);
            transition: all .5s ease-in;
}
.newsEnter {
    opacity: 1;
    transform: translateX(0%);
}
.blueBtn {
    text-align: center;
    background: #a70d0d;
    border-bottom: 1px solid #d7d7d7;
    font-weight: 500;
    color: #eeeeee;
    padding: 9px;
    cursor: pointer;
    font-size: 15px;
    display: block;
    text-decoration: none;
}
.newsListItem {
    border-bottom: 1px solid #121212;
    background: #fff;
    margin-top: 0px;
    padding: 8px 5px 0 5px;
}
.titl {
    font-size: 13px;
    line-height: 21px;
    margin: 5px 0;
    color: #525252;
}
.link {
    text-decoration: none;
    flex-grow: 1;
}
.flexWrapper {
    display: flex;
    border-bottom: 2px solid #d5d5dd;
    background: #fff;
}

.left {
    background-size: cover !important;
}
.left div{
    width: 90px;
    height: 90px;
    background-size: 40px !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
}

.right {
    flex-grow: 1;
    padding: 10px;
}

.flexWrapper h2 {
    font-size: 12px;
    color: #525252;
}
